
/* 人行证信 - 如何注册证信账号 - 2018-3-21 */
import React, { Component } from 'react';
import './style.scss';
class Rhzx extends Component {
  render() {
    return (
      <div className="rhzx">
        {/* 标题 */}
        <h2 className="title">如何注册证信中心账号</h2>
        <p className="brief">请登录该征信中心网址 <a href="https://ipcrs.pbccrc.org.cn">https://ipcrs.pbccrc.org.cn</a> 参照以下流程获取账号密码及身份验证码</p>

        {/* 步骤 1 */}
        <div className="step">
          <h3 className="title">
            <span className="stepnum">1</span>
            平台页面
          </h3>

          <div className="detail noline">
            <h4 className="desc">点击“马上开始”</h4>
            <img className="nopadding" src={require('./images/pic_1@3x.jpg')}/>
          </div>
        </div>

        {/* 步骤 2 */}
        <div className="step">
          <h3 className="title">
            <span className="stepnum">2</span>
            用户注册
          </h3>

          <div className="detail">
            <h4 className="desc">点击"新用户注册"。</h4>
            <img src={require('./images/pic_2.1@3x.jpg')}/>
          </div>

          <div className="detail">
            <h4 className="desc">填写标识信息、验证码，阅读服务协议，点击"下一步"。</h4>
            <img src={require('./images/pic_2.2@3x.jpg')}/>
          </div>

          <div className="detail">
            <h4 className="desc">填写用户基本信息，然后点击"提交"。</h4>
            <img src={require('./images/pic_2.3@3x.jpg')}/>
          </div>

          <div className="detail noline">
            <h4 className="desc">注册完成，页面提示用户注册成功。</h4>
            <img src={require('./images/pic_2.4@3x.jpg')}/>
          </div>
        </div>

        {/* 步骤 3 */}
        <div className="step">
          <h3 className="title">
            <span className="stepnum">3</span>
            新用户查询信用信息产
          </h3>

          <div className="detail">
            <h4 className="desc">填写登录名、密码、验证码，点击"登录"。</h4>
            <img src={require('./images/pic_3.1@3x.jpg')}/>
          </div>

          <div className="detail">
            <h4 className="desc">新手导航。</h4>
            <img src={require('./images/pic_3.2@3x.jpg')}/>
            <p>点击"确定"，进入以下页面，点击"下一步"。</p>
          </div>

          <div className="detail">
            <h4 className="desc">安全等级变更</h4>
            <img src={require('./images/pic_4@3x.jpg')}/>
            <p>选择数字证书验证，进入以下页面：</p>
            <img src={require('./images/pic_4.1@3x.jpg')}/>
            <p>仔细核对本人信息，正确回答验证问题，点击"下一步"。</p>
          </div>

          <div className="detail">
            <h4 className="desc">选择个人信用信息产品，提交查询申请。</h4>
            <img src={require('./images/pic_5@3x.jpg')}/>
          </div>

          <div className="detail noline">
            <h4 className="desc nomargin">获取信用信息产品（非实时）</h4>
            <p>提交查询申请后的24小时后，客户会收到身份验证码</p>
          </div>
        </div>

      </div>
    );
  }
}

export default Rhzx;



